import {
	newsById,
	checkLike,
	newsLiked
} from '@/api/news'

import {
	mapGetters
} from 'vuex'

import {
	minusDate,
	formatterDate
} from '@/utils/date';
import uParse from '@/third/gaoyia-parse/parse.vue'
export default {
	components: {
		uParse
	},
	data() {
		return {
			article: {},
			isLiked: false
		}
	},
	onLoad() {
		newsById(this.$Route.query).then(res => {
			this.article = res.data

			checkLike({
				id: this.article.id,
				userId: this.userInfo.id
			}).then(res => {
			   if(res.data.status === 'false'){
				   this.isLiked = false
			   }else{
				   this.isLiked = true
			   }
				
			})
		})

	},
	computed: {
		...mapGetters({
			userInfo: 'user/userInfo',
			isLogin: 'user/isLogin'
		}),
		createDate() {
			//格式化时间
			let {
				createDate
			} = this.article
			return formatterDate(createDate, 'YY-MM-DD')
		}
	},
	methods: {
		preview(src, e) {
			// do something
			console.log(src)
		},
		navigate(href, e) {
			// do something
		},
		//处理点赞事件
		handlerLike() {
			newsLiked({
				id: this.article.id,
				userId: this.userInfo.id
			}).then(res => {
			   if(res.data.status === 'false'){
				   this.isLiked = false
			   }else{
				   this.isLiked = true
			   }
				
			})
		}
	}
}
